.blog_main_content{
    width:100%;
    max-width: 100%;
    height:100vh;
    background:url("./img/Blogimg.png") center center no-repeat;
    background-size: cover;
}
.loading_pages_aoe12{
    margin-top:20px;
    text-align:center;
    padding-bottom:20px;
    font-size:24px;
    display: flex;
    justify-content:center;
    font-family: "TTNorms-Regular";
}
.loading_pages_aoe12 div:nth-child(1){
    width:4px;
    height:4px;
    background:black;
    border-radius: 50%;
    margin-top:17px;
    margin-left:5px;
    animation: onechangeanim 1s infinite;
}
.loading_pages_aoe12 div:nth-child(2){
    width:4px;
    height:4px;
    background:black;
    border-radius: 50%;
    margin-top:17px;
    margin-left:5px;
    animation: onechangeanim 1s infinite;
}
.loading_pages_aoe12 div:nth-child(3){
    width:4px;
    height:4px;
    background:black;
    border-radius: 50%;
    margin-top:17px;
    margin-left:5px;
    animation: threechaged 1s infinite;
}
@keyframes onechangeanim{
0% {
       opacity: 0;
    }
    100% {
        opacity: 1;
    }
}
@keyframes twochangeanim{
    0% {
           opacity:1;
        }
        100% {
            opacity: 0;
        }
    }
    @keyframes threechaged{
        0% {
               opacity:1;
            }
            100% {
                opacity: 0;
            }
        }
